<template>
  <div class="document container px-0 px-md-3">
    <el-backtop></el-backtop>
    <div class="px-3 p-md-0">
      <el-row class="row-head">
        <el-col :span="24" class="mt-5 m-0 col-head">
          <div class="link-box">
            <el-button type="text" @click="handlePDFLink">
              <font-awesome-icon :icon="['fas', 'file-pdf']" />
              <span class="ml-2">PDF</span>
            </el-button>
            <el-button type="text" @click="handleCodeLink">
              <font-awesome-icon :icon="['fas', 'code']" />
              <span class="ml-2">Code</span>
            </el-button>
            <el-button type="text" @click="handleDatasetLink">
              <font-awesome-icon :icon="['fas', 'database']" />
              <span class="ml-2">Dataset</span>
            </el-button>
          </div>
          <h1 class="paperTitle">{{ paperTitle }}</h1>
        </el-col>
      </el-row>
      <el-row class="row-detail">
        <div>
          <span class="author">作者：</span>
          <el-button type="text" v-for="item of authors" :key="item.id">
            {{ item.name }}
          </el-button>
        </div>
        <el-col :span="12" class="col-detail">
          <div>
            <div>
              <div class="abstract">摘要：</div>
              <div class="abstract-content">
                <!--展开收起功能-->
                <div
                  :class="showTotal ? 'total-abstract' : 'detailed-abstract'"
                >
                  <div class="abstract-content" :title="abstract" ref="desc">
                    <span class="merchant-desc" v-if="abstract">{{
                      abstract
                    }}</span>
                    <div
                      class="unfold"
                      @click="showTotalAbstract"
                      v-if="showExchangeButton"
                    >
                      <p>{{ exchangeButton ? "展开" : "收起" }}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <span class="author">关键词：</span>
              <el-tag
                class="ml-2 keys-tag"
                v-for="(item, index) of keys"
                :key="index"
                effect="plain"
                size="mini"
              >
                {{ item }}
              </el-tag>
            </div>
            <div>
              <span class="author">出版组织：</span>
              <el-button type="text">{{ publicationOrg }}</el-button>
            </div>
            <div>
              <span class="author">年份：{{ year }}</span>
            </div>
          </div>
        </el-col>
        <el-col :span="12" class="col-detail">
          <div class="video-box">
            <Video :videoUrl="videoUrl" />
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import Video from "../components/Video";

export default {
  name: "DocumentDetails",
  components: {
    Video
  },
  data: function() {
    return {
      detailDoc: [],
      paperTitle:
        "Boundary Enhanced Neural Span Classification for Nested Named Entity Recognition",
      authors: [
        {
          id: 1,
          name: "Hinton"
        },
        {
          id: 2,
          name: "Lecun"
        }
      ],
      keys: ["key1", "key2", "key3"],
      abstract:
        'Cross-correlation analyses of successive satellite images of the ocean surface can provide estimates of surface currents by tracking features of temperature or ocean color. While the technique successfully identifies many true vectors, it also can produce a large number of "rogue" vectors through the erroneous matching of similar-shaped surface features. Several techniques have been suggested to detect these rogue vectors and eliminate them from the analyses. These include nearest-neighbor matching, velocity filtering, and limits on the minimum cross-correlation coefficient that is acceptable. A pair of concurrent Advanced Very High Resolution Radiometer (AVHRR) images of the Leeuwin Current off the west coast of Australia is used to show that a new technique, based on reciprocity, outperforms other techniques in many situations. ',
      publicationOrg: "NeurIPS", //出版组织
      year: 2020,
      pdfUrl:
        "http://laplab.ucsd.edu/articles/Pashler_Johnston_Attention1998.pdf",
      pdfPath: "",
      publicationUrl:
        "https://papers.nips.cc/paper/2020/hash/3ad7c2ebb96fcba7cda0cf54a2e802f5-Abstract.html",
      codeUrl: "https://github.com/zhang0jhon/AttentionOCR",
      datasetUrl: "https://github.com/zhang0jhon/AttentionOCR",
      videoUrl:
        "https://ksv-video-publish.cdn.bcebos.com/a977986c4eb9e8ecd3f0a4853e752a6ab767c14a.mp4?auth_key=1659094636-0-0-e76d93ce627efcc28663d96501b2c447",
      videoPath: "",
      pdfText: "",
      showTotal: false, // 简介全部显示
      exchangeButton: true, // 展开/收起文字改变
      showExchangeButton: true // 是否显示 展开/收起 文字
    };
  },
  mounted: function() {
    this.detailCheck();
  },
  methods: {
    detailCheck() {
      const detail = this.$route.params.detail;
      console.log(detail);
      if (!detail) {
        console.log("Redirect to home.");
        this.$router.push({ path: "/" });
        return;
      }
      console.log("Start detailing...");
      this.detailDoc = detail;
    },
    handlePDFLink: function() {
      window.open(this.pdfUrl);
    },

    handleCodeLink: function() {
      window.open(this.codeUrl);
    },

    handleDatasetLink: function() {
      window.open(this.datasetUrl);
    },

    showTotalAbstract() {
      this.showTotal = !this.showTotal;
      this.exchangeButton = !this.exchangeButton;
    }
  },
  watch: {
    abstract: function() {
      this.$nextTick(() => {
        if (!this.$refs.desc) {
          return;
        }
        const descHeight = window
          .getComputedStyle(this.$refs.desc)
          .height.replace("px", "");
        if (parseInt(descHeight) * 2 > 120) {
          this.showExchangeButton = true;
          this.exchangeButton = true;
          this.showTotal = false;
        } else {
          this.showExchangeButton = false;
          this.showTotal = true;
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
.paperTitle {
  // color:#009688;
  font-weight: bold;
}
.abstract-content {
  height: auto;
  text-align: justify;
  text-justify: inter-ideograph;
  color: #666666;
}

.abstract {
  float: left;
  color: #999999;
}

.author {
  color: #999999;
}

.video-box {
  width: 90%;
  text-align: center;
  margin: auto;
}

.link-box {
  margin: 0.8rem 0;
  .el-button--text {
    padding: 0.25rem 0;
    font-size: 1rem;
  }
}

//展开收起部分样式
.total-abstract {
  height: auto;
  overflow: hidden;
  font-size: 16px;
  color: #666;
  //margin-top: 30px;
  .abstract-content {
    .merchant-desc {
      width: 100%;
      line-height: 25px;
    }
  }
  .unfold {
    display: block;
    float: right;
    width: 50px;
    height: 25px;
    z-index: 8;
    text-align: center;
    p {
      margin: 0;
      line-height: 25px;
      color: #009688;
    }
  }
}
.detailed-abstract {
  font-size: 16px;
  color: #666;
  position: relative;
  overflow: hidden;
  //margin-top: 30px;
  .abstract-content {
    //最大高度设为10倍的行间距
    max-height: 200px;
    line-height: 25px;
    word-wrap: break-word;
    word-break: break-all;
    background-color: #fff;
    color: #fff;
    overflow: hidden;
    .merchant-desc {
      width: 100%;
      line-height: 25px;
    }
    &::after,
    &::before {
      content: attr(title);
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      color: #666;
    }
    &::before {
      display: block;
      overflow: hidden;
      z-index: 1;
      max-height: 150px;
      background: #fff;
    }
    &::after {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      height: 200px;
      // 截取行数
      -webkit-line-clamp: 8;
      text-overflow: ellipsis;
      box-sizing: border-box;
      // 行首缩进字符数，值为[(截取行数-1)*尾部留空字符数]
      text-indent: -6em;
      // 尾部留空字符数
      padding-right: 3em;
    }
    .unfold {
      z-index: 8;
      width: 50px;
      height: 25px;
      outline: 0;
      position: absolute;
      right: 0;
      bottom: 0;
      text-align: center;
      p {
        margin: 0;
        color: #009688;
      }
    }
  }
}
</style>
