<template>
  <div class="history position-relative">
    <el-row class="row-head mt-4">
      <el-col :xs="10" :sm="16" class="col-head">
        <h4 class="m-0">{{ $t("history.SearchHistory") }}</h4>
      </el-col>
      <el-col :xs="14" :sm="8" class="col-head">
        <el-input
          v-model="historySearch"
          size="mini"
          v-bind:placeholder="$t('history.PleaseInput')"
        >
        </el-input>
      </el-col>
    </el-row>
    <el-row class="row-head mt-3">
      <el-col :span="24" class="col-head">
        <el-table
          :data="
            historyTableData.filter(
              data =>
                !historySearch ||
                data.query.toLowerCase().includes(historySearch.toLowerCase())
            )
          "
          :cell-style="{ padding: '0.375rem 0' }"
          :show-header="false"
          max-height="600"
          class=""
        >
          <el-table-column
            type="index"
            :index="indexMethod"
            width="40"
          ></el-table-column>
          <el-table-column prop="type" width="96">
            <template v-slot:default="scope">
              <el-tag v-bind:type="scope.row.tag">{{ scope.row.type }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="query" min-width="200"></el-table-column>
          <el-table-column
            prop="time"
            align="right"
            width="160"
            v-if="flagTableTime"
          >
          </el-table-column>
          <el-table-column align="right">
            <template v-slot:default="scope">
              <el-button-group>
                <el-button size="mini" @click="handleSearch(scope.$index)">
                  <font-awesome-icon :icon="['fas', 'search']" />
                </el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index)"
                >
                  <font-awesome-icon :icon="['far', 'trash-alt']" />
                </el-button>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row class="row-clear mt-3">
      <el-col :span="6" class="col-clear">
        <el-button
          type="text"
          v-if="!!historyTableData.length"
          @click="handleClear()"
          class="ml-2"
        >
          <font-awesome-icon :icon="['fas', 'trash']" class="mr-2" />
          <span>{{ $t("history.ClearAll") }}</span>
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "History",
  props: ["histories"],
  data: function() {
    return {
      flagTableTime: !window.matchMedia("(max-width: 992px)").matches,
      historySearch: "",
      historiesData: this.histories,
      tagDist: {
        doc: "",
        auth: "success",
        venue: "warning"
      },
      pushDist: {
        doc: "DocumentResults",
        auth: "AuthorResults",
        venue: "VenueResults"
      }
    };
  },
  computed: {
    typeDist: function() {
      return {
        doc: this.$t("history.tDoc"),
        auth: this.$t("history.tAuth"),
        venue: this.$t("history.tVenue")
      };
    },
    historyTableData: {
      get() {
        var tableData = [];
        this.historiesData.forEach(function(history) {
          switch (history.type) {
            case "doc":
              tableData.push({
                tag: this.tagDist[history.type],
                type: this.typeDist[history.type],
                query: this.convertQueryDoc(history.query),
                time: history.time
              });
              break;
            case "auth":
            case "venue":
              tableData.push({
                tag: this.tagDist[history.type],
                type: this.typeDist[history.type],
                query: history.query,
                time: history.time
              });
              break;
          }
        }, this);
        return tableData;
      },
      set: function(newValue) {
        if (Number.isInteger(newValue)) {
          this.historiesData.splice(newValue, 1);
        } else if (Array.isArray(newValue)) {
          this.historiesData = [];
        }
      }
    }
  },
  mounted() {
    window.onresize = () => {
      this.flagTableTime = !window.matchMedia("(max-width: 992px)").matches;
    };
  },
  methods: {
    indexMethod(index) {
      return this.historyTableData.length - index;
    },
    convertQueryDoc(query) {
      var str = "";
      query.forEach(function(item) {
        if (Object.prototype.hasOwnProperty.call(item, "logic")) {
          str += " " + item.logic + " ";
        }
        str += item.type + " ( " + item.input + " ) ";
      }, this);
      str = str.replace(/(^\s*)|(\s*$)/g, "");
      str = str.replace(/\s+/g, " ");
      return str;
    },
    handleSearch(index) {
      const history = this.historiesData[index];
      this.$router.push({
        name: this.pushDist[history.type],
        params: { type: history.type, query: history.query }
      });
    },
    handleDelete(index) {
      this.$confirm(
        this.$t("history.DeleteConfirm"),
        this.$t("history.Warning"),
        {
          confirmButtonText: this.$t("history.OK"),
          cancelButtonText: this.$t("history.Cancel"),
          type: "warning"
        }
      )
        .then(() => {
          this.$cookies.remove("history*" + this.historyTableData[index].time);
          this.historyTableData = index;
          this.$message({
            type: "success",
            message: this.$t("history.DeleteOK")
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: this.$t("history.DeleteCancel")
          });
        });
    },
    handleClear() {
      this.$confirm(
        this.$t("history.ClearConfirm"),
        this.$t("history.Warning"),
        {
          confirmButtonText: this.$t("history.OK"),
          cancelButtonText: this.$t("history.Cancel"),
          type: "warning"
        }
      )
        .then(() => {
          const historyKeys = this.$cookies
            .keys()
            .filter(e => e.substr(0, 8) == "history*");
          historyKeys.forEach(function(key) {
            this.$cookies.remove(key);
          }, this);
          this.historyTableData = [];
          this.$message({
            type: "success",
            message: this.$t("history.ClearOK")
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: this.$t("history.ClearCancel")
          });
        });
    }
  }
};
</script>

<style scoped lang="scss">
.history {
  .el-button--text {
    padding: 0.25rem 0;
    font-size: 1rem;
  }
}
</style>
