<template>
  <div class="documents container px-0 px-md-3">
    <el-backtop></el-backtop>
    <div class="px-3 p-md-0" v-loading.fullscreen.lock="fullLoading">
      <el-row class="row-head">
        <el-col :span="24" class="col-head">
          <h1 class="mt-5 m-0">
            {{ TotalDoc.toLocaleString() }} {{ $t("list.TotalDoc") }}
          </h1>
          <p v-if="!!queryDoc[0]" class="text-muted mt-4 m-0">
            {{ queryDoc[0] }}
          </p>
        </el-col>
        <el-col :span="24" class="col-edit mt-3">
          <el-button type="text" class="mr-1" @click="handleEdit()">
            <font-awesome-icon :icon="['fas', 'edit']" class="mr-2" />
            <span>{{ $t("list.Edit") }}</span>
          </el-button>
          <el-button
            type="text"
            @click="handleExport()"
            :disabled="!multipleSelection.length"
          >
            <font-awesome-icon :icon="['fas', 'download']" class="mr-2" />
            <span>{{ $t("list.Export") }}</span>
          </el-button>
        </el-col>
      </el-row>
      <el-row class="row-list">
        <el-col :span="24" class="col-list mt-5">
          <el-table
            v-loading="tableLoad"
            :data="resultTableData"
            :cell-style="{ padding: '0.5rem 0', 'vertical-align': 'top' }"
            :header-cell-style="{
              'font-size': '1rem',
              'font-weight': '400',
              color: '#212529'
            }"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="30"></el-table-column>
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40"
            ></el-table-column>
            <el-table-column type="expand" width="20">
              <template v-slot:default="props">
                <el-form label-position="left" inline class="table-expand">
                  <el-form-item v-bind:label="$t('list.Title')">
                    <span>{{ props.row.title }}</span>
                  </el-form-item>
                  <el-form-item v-bind:label="$t('list.Authors')">
                    <span
                      v-for="(author, index) in props.row.authors"
                      :key="index"
                    >
                      <span v-if="index">, </span>
                      <span>{{ author.name }}</span>
                    </span>
                  </el-form-item>
                  <el-form-item
                    v-if="!!props.row.abstract"
                    v-bind:label="$t('list.Abstract')"
                  >
                    <p style="line-height: 1.5rem">{{ props.row.abstract }}</p>
                  </el-form-item>
                  <el-form-item v-bind:label="$t('list.Year')">
                    <span>{{ props.row.year }}</span>
                  </el-form-item>
                  <el-form-item v-bind:label="$t('list.Source')">
                    <span>{{ props.row.venue }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column v-bind:label="$t('list.Title')" min-width="200">
              <template v-slot:default="scope">
                <el-button type="text" @click="handleTitle(scope.$index)">
                  <p class="h6 text-left text-wrap m-0">
                    {{ scope.row.title }}
                  </p>
                </el-button>
              </template>
            </el-table-column>
            <el-table-column v-bind:label="$t('list.Authors')">
              <template v-slot:default="scope">
                <ul class="list-unstyled">
                  <li v-for="(author, index) in scope.row.authors" :key="index">
                    <span>{{ author.name }}</span>
                  </li>
                </ul>
              </template>
            </el-table-column>
            <el-table-column
              prop="year"
              v-bind:label="$t('list.Year')"
              width="60"
            ></el-table-column>
            <el-table-column
              prop="venue"
              v-bind:label="$t('list.Source')"
              width="70"
            ></el-table-column>
            <el-table-column align="right" width="60">
              <template v-slot:default="scope">
                <div class="button-group-vertical mr-2">
                  <el-button
                    size="mini"
                    type="primary"
                    @click="handleLink(scope.row.url)"
                    :disabled="!scope.row.url"
                  >
                    <font-awesome-icon :icon="['fas', 'link']" />
                  </el-button>
                  <el-button
                    size="mini"
                    type="success"
                    @click="handleLink(scope.row.code_url)"
                    class="m-0"
                    :disabled="!scope.row.code_url"
                  >
                    <font-awesome-icon :icon="['fas', 'code']" />
                  </el-button>
                  <el-button
                    size="mini"
                    type="warning"
                    @click="handleLink(scope.row.pdf_url)"
                    class="m-0"
                    :disabled="!scope.row.pdf_url"
                  >
                    <font-awesome-icon :icon="['far', 'file-pdf']" />
                  </el-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row class="row-page">
        <el-col :span="24" class="col-page mt-5">
          <el-pagination
            background
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next, jumper"
            :total="TotalDoc"
            class="text-center"
          >
          </el-pagination>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocumentResults",
  components: {},
  data: function() {
    return {
      fullLoading: false,
      queryDoc: [],
      TotalDoc: 0,
      tableLoad: false,
      multipleSelection: [],
      resultTableData: [],
      currentPage: 1
    };
  },
  mounted: function() {
    this.resultCheck();
  },
  methods: {
    resultCheck() {
      const type = this.$route.params.type;
      const query = this.$route.params.query;
      if (!query || type != "doc") {
        console.log("Redirect to home.");
        this.$router.push({ path: "/" });
        return;
      }
      console.log("Start searching...");
      this.fullLoading = true;
      this.queryDoc = query;
      this.querySubmit();
    },
    querySubmit(from = 0) {
      this.axios
        .get(
          "/query?type=FULL_TEXT&query=" +
            this.queryDoc[0].input +
            "&from=" +
            from +
            "&size=" +
            "10"
        )
        .then(response => {
          console.log(response.data);
          this.TotalDoc = response.data.hits["@total"];
          this.resultTableData = response.data.hits.hit;
        })
        .catch(error => {
          console.log(error);
          this.$message.error(this.$t("list.UnknownError"));
        })
        .finally(() => {
          this.fullLoading = false;
          this.tableLoad = false;
        });
    },
    handleEdit() {
      this.$router.push({ path: "/" });
    },
    handleExport() {
      this.$parent.handleExport(this.multipleSelection);
    },
    indexMethod(index) {
      return (this.currentPage - 1) * 10 + index + 1;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleTitle(index) {
      this.$router.push({
        name: "DocumentDetails",
        params: { detail: this.resultTableData[index] }
      });
    },
    handleLink(link) {
      window.open(link, "_blank");
    },
    handlePDF(index, row) {
      console.log(index, row);
    },
    handleCurrentChange(val) {
      this.tableLoad = true;
      this.querySubmit((val - 1) * 10);
    }
  }
};
</script>

