<template>
  <div class="authors container px-0 px-md-3">
    <el-backtop></el-backtop>
    <div class="px-3 p-md-0" v-loading.fullscreen.lock="fullLoading">
      <el-row class="row-head">
        <el-col :span="24" class="col-head">
          <h1 class="mt-5 m-0">
            {{ TotalAuth.toLocaleString() }} {{ $t("list.TotalAuth") }}
          </h1>
          <p v-text="$route.params.query" class="text-muted mt-4 m-0"></p>
        </el-col>
        <el-col :span="24" class="col-edit mt-3">
          <el-button type="text" class="mr-1" @click="handleEdit()">
            <font-awesome-icon :icon="['fas', 'edit']" class="mr-2" />
            <span>{{ $t("list.Edit") }}</span>
          </el-button>
          <el-button
            type="text"
            @click="handleExport()"
            :disabled="!multipleSelection.length"
          >
            <font-awesome-icon :icon="['fas', 'download']" class="mr-2" />
            <span>{{ $t("list.Export") }}</span>
          </el-button>
        </el-col>
      </el-row>
      <el-row class="row-list">
        <el-col :span="24" class="col-list mt-5">
          <el-table
            v-loading="tableLoad"
            :data="resultTableData"
            :cell-style="{ padding: '0.5rem 0', 'vertical-align': 'top' }"
            :header-cell-style="{
              'font-size': '1rem',
              'font-weight': '400',
              color: '#212529'
            }"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="30"></el-table-column>
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40"
            ></el-table-column>
            <el-table-column
              prop="name"
              v-bind:label="$t('list.AuthName')"
            ></el-table-column>
            <el-table-column prop="orcid" label="ORCID"></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row class="row-page">
        <el-col :span="24" class="col-page mt-5">
          <el-pagination
            background
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next, jumper"
            :total="TotalAuth"
            class="text-center"
          >
          </el-pagination>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: "AuthorResults",
  components: {},
  data: function() {
    return {
      fullLoading: false,
      queryAuth: [],
      TotalAuth: 0,
      tableLoad: false,
      multipleSelection: [],
      resultTableData: [],
      currentPage: 1
    };
  },
  mounted: function() {
    this.resultCheck();
  },
  methods: {
    resultCheck() {
      const type = this.$route.params.type;
      const query = this.$route.params.query;
      if (!query || type != "auth") {
        console.log("Redirect to home.");
        this.$router.push({ path: "/" });
        return;
      }
      console.log("Start searching...");
      this.fullLoading = true;
      this.queryAuth = query;
      this.querySubmit();
    },
    querySubmit(from = 0) {
      this.axios
        .get(
          "/authors?type=AUTHORS&query=" +
            this.queryAuth +
            "&from=" +
            from +
            "&size=" +
            "10"
        )
        .then(response => {
          console.log(response.data);
          this.TotalAuth = response.data.hits["@total"];
          this.resultTableData = response.data.hits.hit;
        })
        .catch(error => {
          console.log(error);
          this.$message.error(this.$t("list.UnknownError"));
        })
        .finally(() => {
          this.fullLoading = false;
          this.tableLoad = false;
        });
    },
    handleEdit() {
      this.$router.push({ path: "/" });
    },
    handleExport() {
      this.$parent.handleExport(this.multipleSelection);
    },
    indexMethod(index) {
      return (this.currentPage - 1) * 10 + index + 1;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleCurrentChange(val) {
      this.tableLoad = true;
      this.querySubmit((val - 1) * 10);
    }
  }
};
</script>

<style scoped lang="scss">
.authors {
  .row-head {
    .el-button--text {
      padding: 0.25rem 0;
      font-size: 1rem;
    }
  }
  .row-list /deep/ {
    .el-table-column--selection {
      .cell {
        padding-left: 0.5rem;
        padding-right: 0.125rem;
        width: 100%;
      }
      & + td {
        .cell {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }
  }
}
</style>
