<template>
  <div class="year-range">
    <el-row :gutter="20" class="row-year">
      <el-col :xs="24" :sm="12" class="col-year">
        <small class="teal-text">{{ $t("time.YearFrom") }}</small>
        <el-date-picker
          v-model="yearFrom"
          type="year"
          v-bind:placeholder="$t('time.AllYears')"
          format="yyyy"
          value-format="yyyy"
          @change="selectYear"
          :picker-options="pickerOptions"
          class="mt-2 w-100"
        >
        </el-date-picker>
      </el-col>
      <el-col :xs="24" :sm="12" class="col-year mt-3 mt-md-0">
        <small class="teal-text">{{ $t("time.YearTo") }}</small>
        <el-date-picker
          v-model="yearTo"
          type="year"
          v-bind:placeholder="$t('time.SoFar')"
          format="yyyy"
          value-format="yyyy"
          @change="selectYear"
          :picker-options="pickerOptions"
          class="mt-2 w-100"
        >
        </el-date-picker>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "YearRange",
  data: function() {
    return {
      yearFrom: "",
      yearTo: "",
      pickerOptions: {
        disabledDate(time) {
          return (
            time.getFullYear() < "1960" ||
            time.getFullYear() > new Date().getFullYear()
          );
        }
      }
    };
  },
  methods: {
    selectYear() {
      if (
        this.yearFrom - 0 > this.yearTo - 0 &&
        !!this.yearFrom &&
        !!this.yearTo
      ) {
        this.$message.error(this.$t("time.ShouldGreater"));
        this.yearTo = "";
      }
    }
  }
};
</script>

<style scoped lang="scss"></style>
