import Vue from "vue";
import VueRouter from "vue-router";
import Search from "../views/Search.vue";
import DocumentResults from "../views/DocumentResults.vue";
import DocumentDetails from "../views/DocumentDetails.vue";
import AuthorResults from "../views/AuthorResults.vue";
import VenueResults from "../views/VenueResults.vue";
import About from "../views/About.vue";
import Contact from "../views/Contact.vue";
import Mistake from "../views/Mistake.vue";

Vue.use(VueRouter);

const routerPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error);
};

const routes = [
  {
    path: "/",
    name: "Search",
    component: Search,
    meta: {
      title: "Home",
      keepAlive: true
    }
  },
  {
    path: "/documents",
    name: "DocumentResults",
    component: DocumentResults,
    meta: {
      title: "DocResults",
      keepAlive: false
    }
  },
  {
    path: "/authors",
    name: "AuthorResults",
    component: AuthorResults,
    meta: {
      title: "AuthResults",
      keepAlive: false
    }
  },
  {
    path: "/venues",
    name: "VenueResults",
    component: VenueResults,
    meta: {
      title: "VenueResults",
      keepAlive: false
    }
  },
  {
    path: "/document",
    name: "DocumentDetails",
    component: DocumentDetails,
    meta: {
      title: "DocDetails",
      keepAlive: false
    }
  },
  {
    path: "/about",
    name: "About",
    component: About,
    meta: {
      title: "About",
      keepAlive: false
    }
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact,
    meta: {
      title: "Contact",
      keepAlive: false
    }
  },
  {
    path: "/error",
    name: "Mistake",
    component: Mistake,
    meta: {
      title: "Home",
      keepAlive: false
    }
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
