                </el-row>
                <el-row :gutter="20" class="row-input mt-3">
                  <el-col :xs="24" :sm="8" :md="6" class="col-type">
                    <small class="teal-text">{{
                      $t("home.SearchWithin")
                    }}</small>
                    <el-select
                      v-model="item.typeDoc"
                      v-bind:placeholder="$t('home.PleaseChoose')"
                      class="mt-2 w-100"
                    >
                      <el-option
                        v-for="type in item.typeOptions"
                        :key="type.value"
                        :label="type.label"
                        :value="type.value"
                      >
                      </el-option>
                    </el-select>
                  </el-col>
                  <el-col
                    :xs="24"
                    :sm="16"
                    :md="18"
                    class="col-input mt-3 mt-md-0"
                  >
                    <small class="teal-text">{{
                      $t("home.SearchKeywords")
                    }}</small>
                    <el-input
                      v-model="item.inputDoc"
                      v-bind:placeholder="$t('home.PleaseInput')"
