            </el-tab-pane>
            <el-tab-pane
              v-bind:label="$t('home.AuthTab')"
              name="authors"
              class="auth-tab mt-3"
            >
              <!-- Input -->
              <el-row class="row-input">
                <el-col :span="24" class="col-input">
