                  </el-input>
                </el-col>
              </el-row>
              <!-- Search -->
              <el-row class="row-search">
                <el-col :span="24" class="col-search d-flex mt-4">
                  <el-button type="text" @click="handleResetAuth">
                    <font-awesome-icon
                      :icon="['fas', 'undo-alt']"
                      class="mr-2"
                    />
                    <span>{{ $t("home.Reset") }}</span>
                  </el-button>
                  <el-button
                    type="primary"
                    class="ml-auto"
                    @click="handleQueryAuth"
                  >
                    <font-awesome-icon
                      :icon="['fas', 'search']"
                      size="sm"
                      class="mr-1"
                    />
                    <span>{{ $t("home.Search") }}</span>
                  </el-button>
                </el-col>
              </el-row>
            </el-tab-pane>
            <el-tab-pane
              v-bind:label="$t('home.VenueTab')"
              name="venues"
              class="venue-tab mt-3"
            >
              <!-- Input -->
              <el-row class="row-input">
                <el-col :span="24" class="col-input">
                  <small class="teal-text">{{
                    $t("home.SearchKeywords")
                  }}</small>
                  <el-input
                    v-model="inputVenue"
                    v-bind:placeholder="$t('home.PleaseInput')"
                    @keypress.enter.native="handleQueryVenue"
                    class="mt-2 w-100"
                  >
                  </el-input>
                </el-col>
              </el-row>
              <!-- Search -->
              <el-row class="row-search">
                <el-col :span="24" class="col-search d-flex mt-4">
                  <el-button type="text" @click="handleResetVenue">
                    <font-awesome-icon
                      :icon="['fas', 'undo-alt']"
                      class="mr-2"
                    />
                    <span>{{ $t("home.Reset") }}</span>
                  </el-button>
                  <el-button
                    type="primary"
                    class="ml-auto"
                    @click="handleQueryVenue"
                  >
                    <font-awesome-icon
                      :icon="['fas', 'search']"
                      size="sm"
                      class="mr-1"
                    />
                    <span>{{ $t("home.Search") }}</span>
                  </el-button>
                </el-col>
              </el-row>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
      <el-divider class="mt-4"></el-divider>
      <el-row class="row-hist mt-4">
        <el-col :xs="24" :sm="{ span: 22, offset: 2 }" class="col-hist">
          <History v-bind:histories="historyTableData" />
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import YearRange from "@/components/YearRange.vue";
import History from "@/components/History.vue";

export default {
  name: "Search",
  components: {
    YearRange,
    History
  },
  data: function() {
    return {
      activeTab: "documents",
      // Document
      typeDoc: "ALL",
      inputDoc: "",
      logicDoc: "AND",
      listDocs: [],
      // Auth
      inputAuth: "",
      // Venue
      inputVenue: "",
      // Year
      flagYearDoc: false
    };
  },
  computed: {
    typeOptions: function() {
      return [
        {
          value: "ALL",
          label: this.$t("home.tALL")
        },
        {
          value: "TITLE_ABS_KEY",
          label: this.$t("home.tTITLE_ABS_KEY")
        },
        {
          value: "AUTHOR",
          label: this.$t("home.tAUTHOR")
        },
        {
          value: "Source",
          label: this.$t("home.tSource")
        },
        {
          value: "TITLE",
          label: this.$t("home.tTITLE")
        },
        {
          value: "ABS",
          label: this.$t("home.tABS")
        }
      ];
    },
    logicOptions: function() {
      return [
        {
          value: "AND",
          label: this.$t("home.lAND")
        },
        {
          value: "OR",
          label: this.$t("home.lOR")
        },
        {
          value: "AND NOT",
          label: this.$t("home.lANDNOT")
        }
      ];
    },
    historyTableData: function() {
      const historyKeys = this.$cookies
        .keys()
        .filter(e => e.substr(0, 8) == "history*");
      var historyData = [];
      historyKeys.forEach(function(key) {
        historyData.push(this.$cookies.get(key));
      }, this);
      return historyData.reverse();
    }
  },
  mounted() {
    this.localeInit();
  },
  methods: {
    localeInit() {
      if (this.$cookies.isKey("locale")) {
        const locale = this.$cookies.get("locale");
        if (!locale || !["chs", "cht", "eng", "jpa"].includes(locale)) {
          this.$cookies.remove("locale");
          return;
        }
        this.$i18n.locale = locale;
      }
    },
    handleAddDoc() {
      this.listDocs.push({
        logicOptions: this.logicOptions,
        logicDoc: "AND",
        typeOptions: this.typeOptions,
        typeDoc: "TITLE_ABS_KEY",
        inputDoc: ""
      });
    },
    handleResetDoc() {
      [this.inputDoc, this.flagYearDoc, this.listDocs] = ["", false, []];
    },
    handleResetAuth() {
      [this.inputAuth] = [""];
    },
    handleResetVenue() {
      [this.inputVenue] = [""];
    },
    handleQueryDoc() {
      // List Generate
      var inputList = [{ type: this.typeDoc, input: this.inputDoc }];
      this.listDocs.forEach(function(doc) {
        inputList.push({
          type: doc.typeDoc,
          input: doc.inputDoc,
          logic: doc.logicDoc
        });
      }, this);
      // Empty Check
      var flagInput = false;
      inputList.forEach(function(item) {
        if (!item.input) {
          flagInput = true;
        }
      }, this);
      if (flagInput) {
        this.$message.error(this.$t("home.CannotEmpty"));
        return;
      }
      // Push
      this.historySave("doc", inputList);
      this.$router.push({
        name: "DocumentResults",
        params: { type: "doc", query: inputList }
      });
    },
    handleQueryAuth() {
      this.historySave("auth", this.inputAuth);
      this.$router.push({
        name: "AuthorResults",
        params: { type: "auth", query: this.inputAuth }
      });
    },
    handleQueryVenue() {
      this.historySave("venue", this.inputVenue);
      this.$router.push({
        name: "VenueResults",
        params: { type: "venue", query: this.inputVenue }
      });
    },
    historySave(type, query) {
      const time = new Date().Format("yyyy-MM-dd hh:mm:ss");
      this.$cookies.set(
        "history*" + time,
        {
          type: type,
          query: query,
          time: time
        },
        "3d"
      );
    }
  }
};
</script>

