<template>
  <div class="infobar container px-0 px-md-3 pt-5 pb-4">
    <div class="px-3 p-md-0">
      <!-- Info -->
      <el-row :gutter="30" class="row-info mt-5">
        <el-col :xs="24" :sm="12" class="col-about mb-4 mb-md-0">
          <h5 class="title mb-3">{{ $t("info.About") }}</h5>
          <p class="mb-2">{{ $t("info.Description") }}</p>
          <el-link
            @click="$router.push({ path: '/about' })"
            class="teal-text d-inline-block"
          >
            <h6 class="m-0">{{ $t("info.MoreAbout") }}</h6>
          </el-link>
        </el-col>
        <el-col :xs="12" :sm="6" class="col-lang">
          <h5 class="title mb-3">{{ $t("info.Language") }}</h5>
          <ul class="list-unstyled">
            <li>
              <el-link v-if="!flagCHS" @click="switch2CHS">
                <h6 class="m-0">切换到简体中文</h6>
              </el-link>
            </li>
            <li>
              <el-link v-if="!flagCHT" @click="switch2CHT">
                <h6 class="m-0">切換到正體中文</h6>
              </el-link>
            </li>
            <li>
              <el-link v-if="!flagENG" @click="switch2ENG">
                <h6 class="m-0">Switch to English</h6>
              </el-link>
            </li>
            <li>
              <el-link v-if="!flagJPA" @click="switch2JPA" disabled>
                <h6 class="m-0">日本語に切り替える</h6>
              </el-link>
            </li>
          </ul>
        </el-col>
        <el-col :xs="12" :sm="6" class="col-svc">
          <h5 class="title mb-3">{{ $t("info.Service") }}</h5>
          <ul class="list-unstyled">
            <li>
              <el-link @click="$router.push({ path: '/' })" disabled>
                <h6 class="m-0">{{ $t("info.Help") }}</h6>
              </el-link>
            </li>
            <li>
              <el-link @click="$router.push({ path: '/contact' })">
                <h6 class="m-0">{{ $t("info.Contact") }}</h6>
              </el-link>
            </li>
            <li>
              <el-link @click="$router.push({ path: '/' })" disabled>
                <h6 class="m-0">{{ $t("info.Term") }}</h6>
              </el-link>
            </li>
            <li>
              <el-link @click="$router.push({ path: '/' })" disabled>
                <h6 class="m-0">{{ $t("info.Privacy") }}</h6>
              </el-link>
            </li>
          </ul>
        </el-col>
      </el-row>
      <!-- Info -->
      <el-divider class="my-3"></el-divider>
      <!-- Copyright -->
      <el-row class="row-copy mt-4">
        <el-col :span="24" class="col-copy">
          <p class="mb-2">
            {{ $t("info.Copyright") }} &copy;
            <el-link
              :underline="false"
              href="http://wiki.datahammer.net/"
              target="_blank"
            >
              <span class="h6 m-0">Datahammer</span>
              <font-awesome-icon
                :icon="['fas', 'external-link-alt']"
                size="sm"
                class="ml-1"
              /> </el-link
            >{{ $t("info.RightsReserved") }}
          </p>
          <p class="mb-0">{{ $t("info.Cookies") }}</p>
        </el-col>
      </el-row>
      <!-- Copyright -->
    </div>
  </div>
</template>

<script>
export default {
  name: "Infobar",
  computed: {
    flagCHS: function() {
      return this.$i18n.locale == "chs";
    },
    flagCHT: function() {
      return this.$i18n.locale == "cht";
    },
    flagENG: function() {
      return this.$i18n.locale == "eng";
    },
    flagJPA: function() {
      return this.$i18n.locale == "jpa";
    }
  },
  methods: {
    switch2CHS() {
      this.$i18n.locale = "chs";
      this.convertTitle();
      this.convertLocale("chs");
      this.$message("语言已切换到简体中文。");
    },
    switch2CHT() {
      this.$i18n.locale = "cht";
      this.convertTitle();
      this.convertLocale("cht");
      this.$message("語言已切換到正體中文。");
    },
    switch2ENG() {
      this.$i18n.locale = "eng";
      this.convertTitle();
      this.convertLocale("eng");
      this.$message("The language has been switched to English.");
    },
    switch2JPA() {
      this.$i18n.locale = "eng";
      this.convertTitle();
      this.convertLocale("eng");
      this.$message("表示言語が日本語に切り替わりました。");
    },
    convertTitle() {
      document.title =
        this.$t("router.Base") +
        " - " +
        this.$t("router." + this.$route.meta.title);
    },
    convertLocale(locale = "chs") {
      this.$cookies.remove("locale");
      this.$cookies.set("locale", locale, "1y");
    }
  }
};
</script>

<style scoped lang="scss">
.infobar {
  .row-info {
    ul li {
      margin-bottom: 0.375rem;
      .el-link {
        color: #009688;
      }
    }
  }
}
</style>
